h1, h2, h3, h4, h5, h6 {
	margin-top:10px !important;
}

.premium{
	color:#ff9729 !important;
}

.margin-top10 {
	margin-top:10px;
}

.margin-bottom10 {
	margin-bottom:10px;
}

.width50 {
	width:50%	
}

.font-weight-bold {
	font-weight:bold;
	color:black !important;
}

.navbar-nav {
	li {
		a:hover {
			background-color: red !important;
		}
	}
}

.btn-change-language {
  display: block;
  margin-top: 5px;
  margin-left: auto !important;
  margin-right: auto;
  width: 200px;
  text-align: left;
}

.btn-change-language-cancel {
 display: block;
  margin-top: 15px;
  margin-left: auto !important;
  margin-right: auto;
  width: 200px;
  text-align: center;
}

.border-gray {
	border:1px solid gray;
	margin-top:1px
}

.btn.btn-lg>.lang-lg:after {
	top: 1px;
	left: 12px;
}


.animate-hide {
	-webkit-transition:all cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.25s;
	-moz-transition:all cubic-bezier(0.250, 0.460, 0.450, 0.940) -0.75s;
	-o-transition:all cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.25s;
	transition:all cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.25s;
	line-height:20px;
	opacity:1;
	padding:10px;
	border:1px solid black;
	background:white;
	
	&.ng-hide {
		line-height:0;
		opacity:0;
		padding:0 10px;
	}

}

#pick_words {
	.darker_bkg {
		background-color:#1b8cad;
	}
	.level_button {
		text-align:left;
		font-weight: normal;
		margin:4px;
		width:185px
	}
}
